@charset "UTF-8";
.responsive-gutenberg-addons-block-post-carousel {
  text-align: center; }
  .responsive-gutenberg-addons-block-post-carousel .responsive-gutenberg-addons-block-post-carousel-header {
    overflow: hidden;
    word-break: break-word;
    padding: 15px;
    border-radius: inherit;
    position: relative; }
  .responsive-gutenberg-addons-block-post-carousel .slick-slide {
    width: 100%; }
  .responsive-gutenberg-addons-block-post-carousel .responsive-gutenberg-addons-post-carousel-item.hentry {
    padding: 0px;
    margin: 0px; }

.responsive-gutenberg-addons-post-carousel-inner {
  margin-left: 5px;
  margin-right: 5px; }

.responsive-post-slick-carousel {
  padding: 45px; }

.slick-list,
.slick-slider,
.slick-track {
  position: relative;
  display: block; }

.slick-loading .slick-slide,
.slick-loading .slick-track {
  visibility: hidden; }

.slick-slider {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  overflow: hidden;
  margin: 0;
  padding: 0; }

.slick-list:focus {
  outline: 0; }

.slick-list.dragging {
  cursor: pointer;
  cursor: hand; }

.slick-slider .slick-list,
.slick-slider .slick-track {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  top: 0;
  left: 0; }

.slick-track:after,
.slick-track:before {
  display: table;
  content: ''; }

.slick-track:after {
  clear: both; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir=rtl] .slick-slide {
  float: right; }

.slick-slide img {
  display: block; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: auto;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: #aaa;
  border: 1px solid #aaa;
  outline: none;
  background: transparent; }

.slick-prev:before,
.slick-next:before {
  font-size: 35px;
  line-height: 1;
  opacity: .75;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.slick-prev:before,
[dir="rtl"] .slick-prev:before,
.slick-next:before,
[dir="rtl"] .slick-next:before {
  content: '';
  font-size: 0;
  line-height: 0; }

.slick-prev {
  left: -45px;
  z-index: 1;
  border-radius: 0; }

[dir="rtl"] .slick-prev {
  left: auto;
  right: -45px; }

button.slick-arrow {
  padding: 7px; }

.slick-prev i,
.slick-next i,
.slick-prev i:hover,
.slick-next i:hover,
.slick-prev i:focus,
.slick-next i:focus {
  font-size: 20px;
  width: 1.5em;
  height: 1.5em;
  line-height: 1.6em;
  text-align: center;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box; }

ul.slick-dots {
  padding-top: 0;
  display: block;
  position: absolute;
  margin: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
  text-align: center; }

ul.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0;
  padding: 0;
  cursor: pointer; }

ul.slick-dots li.slick-active button:before {
  opacity: .75;
  color: black; }

ul.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent; }

ul.slick-dots li button:before {
  font-family: none;
  font-size: 30px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•';
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.slick-next {
  right: -45px; }

[dir="rtl"] .slick-next {
  left: -45px;
  right: auto; }

@media (max-width: 976px) {
  .slick-prev {
    left: 15px;
    z-index: 1; }
  [dir="rtl"] .slick-prev {
    left: auto;
    right: 15px; }
  .slick-next {
    right: 15px; }
  [dir="rtl"] .slick-next {
    left: 15px;
    right: auto; } }
