/**
 * Editor styles for the admin
 */

.responsive-gutenberg-addons-block-testimonial {
    margin-bottom: 0;

    .components-button:not(:disabled):not([aria-disabled="true"]):focus {
        background: none;
        box-shadow: none;
    }
    button.responsive-blocks-change-image {
        width:100%;
        height:100%;
        padding:0;
    }
}

.editor-block-list__layout [data-type="responsive-gutenberg-addons/testimonial"] {
    margin-bottom: 1.2em;
}

.responsive-gutenberg-addons-testimonial-info {
    h2.editor-rich-text__tinymce {
        line-height: 1.2;
    }

    p.editor-rich-text__tinymce {
        line-height: 1.6;
    }

    .responsive-gutenberg-addons-testimonial-title
        + .responsive-gutenberg-addons-testimonial-title {
        line-height: 1.8;
    }
}

#editor
    div[class^="wp-block-'responsive-gutenberg-addons'-"].responsive-gutenberg-addons-block-testimonial
    .responsive-gutenberg-addons-add-image
    svg {
    height: 20px;
    width: 20px;
}
