/**
 * Editor styles for the admin
 */

.responsive-gutenberg-addons-block-post-grid {
    .responsive-gutenberg-addons-block-post-grid-title a {
        text-decoration: none;
    }
}

.responsive-gutenberg-addons-hide-query .components-base-control:nth-of-type(3) {
    display: none;
}

.responsive-gutenberg-addons-block-post-grid-markup-settings {
    .components-base-control__help {
        margin-top: 8px;
    }
}

.block-editor__container
    .editor-block-list__block
    .responsive-gutenberg-addons-block-post-grid
    a {
    color: inherit;
}

.responsive-gutenberg-addons-block-post-grid-image {
    position: relative;

    &:hover {
        .responsive-gutenberg-addons-post-grid-no-image-placeholder {
            opacity: 1;
            z-index: 1;
        }
    }
}

.responsive-gutenberg-addons-post-grid-no-image-placeholder {
    padding: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: #f3f3f4;
    transition: 0.2s ease;

    svg {
        margin-bottom: 10px;
    }
}

.responsive-gutenberg-addons-post-grid-no-image-icon {
    position: absolute;
    top: 10px;
    background: #ffe606;
    right: 10px;
    padding: 6px;
    line-height: 1;
    height: 32px;
    transition: 0.2s ease;
    border-radius: 5px;
    z-index: 1;
}

.block-editor__container
    .components-placeholder__fieldset
    .responsive-gutenberg-addons-post-grid-image-help {
    display: inline-block;
    font-weight: bold;

    a {
        color: #0073aa;
        text-decoration: underline;

        &:hover {
            color: #00a0d2;
        }
    }
}
