/**
 * #.# Styles
 *
 * CSS for both Frontend+Backend.
 */

.responsive-column-wrap {

    position: relative;
    overflow:hidden;

    .responsive-column-inner-wrap {
        margin-left: auto;
        margin-right: auto;
        position: relative;
        z-index: 2;
        width: 100%;
    }

    &.responsive-column__align-left	.responsive-column-inner-wrap {
        margin-left: 0;
        margin-right: auto;
    }

    &.responsive-column__align-right .responsive-column-inner-wrap {
        margin-left: auto;
        margin-right: 0;
    }

    .responsive-column__overlay {
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        position: absolute;
    }

    .responsive-column__video-wrap {
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        position: absolute;
        overflow: hidden;
        z-index: 0;
        -webkit-transition: opacity 1s;
        -o-transition: opacity 1s;
        transition: opacity 1s;
    }

    .responsive-column__video-wrap video {
        max-width: 100%;
        width: 100%;
        height: 100%;
        margin: 0;
        line-height: 1;
        border: none;
        display: inline-block;
        vertical-align: baseline;
        -o-object-fit: cover;
        object-fit: cover;
        background-size: cover;
    }
}

.editor-inner-blocks > .editor-block-list__layout > [data-type="responsive-gutenberg-addons/column"] {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding-left: 0;
    padding-right: 0;
    margin-left: -14px;
    margin-right: -14px;
    min-width: 0;
    word-break: break-word;
    overflow-wrap: break-word;
    flex-basis: 100%;
}

@media (max-width: 976px) {
    .responsive-column__align-tablet-left .responsive-column-inner-wrap {
        margin-left: 0;
        margin-right: auto;
    }

    .responsive-column__align-tablet-right .responsive-column-inner-wrap {
        margin-left: auto;
        margin-right: 0;
    }
}

@media (max-width: 767px) {
    .responsive-column__align-mobile-left .responsive-column-inner-wrap {
        margin-left: 0;
        margin-right: auto;
    }

    .responsive-column__align-mobile-right .responsive-column-inner-wrap {
        margin-left: auto;
        margin-right: 0;
    }
}

@media (max-width: 449px) {
    .responsive-columns-wrap.responsive-columns__background-image{
        background-attachment: scroll!important;
    }
}
